<template>
	<div id="index">
		<div class="index">
			<h1 class="text-center">Hi, I'm Jhonarendra</h1>
			<p class="text-center mt-3" v-html="desc_animated"></p>
			<p class="text-center mt-4">
				<a href="" class="btn btn-rounded btn-outline btn-sm btn-white text-white m-2 pulse-anim" :disabled="is_countdown" tag="button">
					<span v-if="is_countdown">Explore {{countdown}}</span>
					<span v-else>Explore</span>
				</a>
				<!-- <router-link to="/about" class="btn btn-rounded btn-outline btn-sm btn-white text-white m-2 pulse-anim">
					Explore
				</router-link> -->
				<a href="" class="btn btn-rounded btn-darkblue btn-sm m-2">Contact</a>
			</p>
			<div class="icon-bottom">
				<p class="text-center">
					<img src="../assets/comp/icon-git.png" height="30" class="mx-2">
					<img src="../assets/comp/icon-vue.png" height="25" class="mx-2">
					<img src="../assets/comp/icon-pwa.png" height="20" class="mx-2">
					<img src="../assets/comp/icon-firestore.png" height="28" class="mx-2">
				</p>
			</div>
		</div>
		<div class="index-comp-area">
			<div class="awan1 comp1"></div>
			<div class="awan2 comp2"></div>
			<div class="cincintitik comp3"></div>
			<div class="lingktrans comp4"></div>
			<div class="lingk3 comp5"></div>
		</div>
	</div>
</template>

<style type="text/css">
	#index {
		position: relative;
		width: 1280px;
		opacity: 0;
		transition: all .5s;
	}
	#index.show {
		opacity: 1;
	}
	.index {
		position: relative;
		padding-top:150px;
		z-index: 99;
	}
	.index-comp-area{
		position: relative;
		margin-top:-400px;
		z-index: 9;
	}
	.icon-bottom {
		position: absolute;
		width: 100%;
		/*bottom:20px;*/
		left:0;
	}
	.icon-bottom img:nth-child(1) {
		opacity: 0;
		animation: fadeSmallingIn .3s forwards; /*forwards nyimpen atribut terakhir css (opacity 1)*/
		animation-delay: 4s;
	}
	.icon-bottom img:nth-child(2) {
		opacity: 0;
		animation: fadeSmallingIn .3s forwards;
		animation-delay: 4.5s;
	}
	.icon-bottom img:nth-child(3) {
		opacity: 0;
		animation: fadeSmallingIn .3s forwards;
		animation-delay: 5s;
	}
	.icon-bottom img:nth-child(4) {
		opacity: 0;
		animation: fadeSmallingIn .3s forwards;
		animation-delay: 5.5s;
	}
	.comp1{
		position: absolute;
		left: -700px;
		top:450px;
		opacity: 0;
		transition: all 2s;
	}
	.comp1.show {
		opacity: 1;
		left: -600px;
	}
	.comp2{
		position: absolute;
		right: -400px;
		opacity: 0;
		top:400px;
		transition: all 2s;
	}
	.comp2.show{
		opacity: 1;
		right: -300px;
	}
	.comp3{
		position: absolute;
		left: 200px;
		top: 100px;
		animation: fade1Jon 1s, rotateJon 20s linear infinite;
	}
	.comp4{
		position: absolute;
		right: 200px;
		top: 100px;
		animation: fade2Jon 2s;
	}
	.comp5{
		position: absolute;
		left: 50%;
		top: 500px;
		animation: fade2Jon 2s, floatingJon 5s infinite;
	}
</style>
    
<script type="text/javascript">
import homeRunningText from './../mixins/homeRunningText' // biar gak panjang, tak pisah aja
export default {
	data(){
		return {
			countdown: 6,
			is_countdown: true,
			pageview: 0,
			minX: this.showPoint.minX,
			maxX: this.showPoint.maxX,
			showEl: {
				comp1: { min: 500, max: 2500 },
				comp2: { min: 1000, max: 2500 }
			}

		}
	},
	props: ['posX', 'posY', 'showPoint'],
	mixins: [homeRunningText],
	methods: {

	},
	mounted(){
		this.descAnimated()

		// nanti ada pencocokan showEl per ukuran layar
	},
	watch: {
		posX: function(x) {
			// #index
			if(x >= this.minX && x <= this.maxX){
				document.querySelector('#index').classList.add('show')
			} else {
				document.querySelector('#index').classList.remove('show')
			}

			let showEl = this.showEl

			// .comp1
			if(x >= showEl.comp1.min && x <= showEl.comp1.max){
				document.querySelector('.comp1').classList.add('show')
			} else {
				document.querySelector('.comp1').classList.remove('show')
			}

			// .comp2
			if(x >= showEl.comp2.min && x <= showEl.comp2.max){
				document.querySelector('.comp2').classList.add('show')
			} else {
				document.querySelector('.comp2').classList.remove('show')
			}
		}
	}
}
</script>