	watch: {
		posX: function(x) {
			let margin = 200 // begitu di munculin, kasi jarak 200px sebelum mulai animasi show
			if(x >= this.minX + margin && x <= this.maxX){
				document.querySelector('#about').classList.add('show')
			} else {
				document.querySelector('#about').classList.remove('show')
			}


			// jo-show

			let rocketPos = window.innerWidth / 2 // posisi roket ditengah
			let widthMargin = 1280 // ditambah lebar section sebelumnya

			// show poin = rocketPos + objPos + widthMargin.
			// jika nilai ini sama dengan posX (yg ditampilin di opjok kanan atas)
			// baru di show

			let elements = document.getElementsByClassName('jo-show')
			let objPos = 0
			let showObj = 0
			for(let i = 0; i < elements.length; i++){
				objPos = elements[i].getBoundingClientRect().x  // posisi x objek
				showObj = rocketPos + objPos + widthMargin
				if('h3-edu' in elements[i].classList){
					console.log(showObj)
				}
				if(x >= showObj){
					elements[i].classList.remove('hide-show')
					if('fade-show' in elements[i].classList == false){
						elements[i].classList.add('fade-show')
					}
				} else {
					elements[i].classList.remove('fade-show')
					elements[i].classList.add('hide-show')
				}
			}


			// var scrollpx = window.scrollY
