<template>
	<div id="app">
		<div id="slide">
			<div id="coin-area"></div>
			<div id="slide-content">
				<Start></Start>
				<Index v-if="posX > showPoint.index.minX && posX < showPoint.index.maxX" :posX="posX" :posY="posY" :showPoint="showPoint.index"></Index>
				<div id="spacer-index" class="spacer" v-if="posX < showPoint.index.minX || posX > showPoint.index.maxX"></div>
				<!-- spacer untuk menahan lebar konten (kan di toggle block htmlnya) -->
				<!-- <Index :posX=posX :posY=posY :showPoint=showPoint.index></Index> -->
				<About v-if="posX > showPoint.about.minX && posX < showPoint.about.maxX" :posX="posX" :posY="posY" :showPoint="showPoint.about"></About>
				<!-- <div class="item">1</div>
				<div class="item">2</div>
				<div class="item">3</div>
				<div class="item">4</div>
				<div class="item">5</div>
				<div class="item">6</div>
				<div class="item">7</div>

				
				<div class="item">8</div>
				<div class="item">9</div>
				<div class="item">10</div>
				<div class="item">11</div>
				<div class="item">12</div> -->
			</div>
		</div>
		<div id="fixed">
			<div id="panel-top-left">
				<div class="d-flex">
					<p class="text-white text-courier">
						x: {{posX}}<br />
						y: {{posY}}
					</p>
				</div>
			</div>
			<div id="panel-top-right">
				<div class="d-flex">
					<div>
						<i class="fa fa-circle mr-2 text-warning"></i>
					</div>
					<div class="text-right" style="margin-left:auto;width: 100px">
						<span class="text-white text-courier">{{score}}</span>
					</div>
				</div>
				<div class="d-flex">
					<div>
						<i class="fa fa-rocket mr-2 text-danger"></i>
					</div>
					<div class="text-right" style="margin-left:auto;width: 100px">
						<span class="text-white text-courier">{{(Math.sqrt(speed*speed) == 0) ? '0' : Math.sqrt(speed*speed)+' 000'}}</span>
					</div>
				</div>
			</div>
			<div id="progress">
				<div id="thumb-rocket" :style="{ backgroundImage: 'url('+require('./assets/comp/thumb-rocket.png')+')', bottom : progress*7.5/10-5+'vh'}"></div>
			</div>



			<div id="controller">
				<div class="text-center">
					<button id="control-up" class="btn btn-controller mb-1" @click="up"><i class="fa fa-chevron-up"></i></button>
				</div>
				<div class="text-center">
					<button id="control-back" class="btn btn-controller mr-4" @click="back"><i class="fa fa-chevron-left"></i></button>
