	@keyframes fadeRightJon{0%,60%{opacity:0;right:-400px}100%{opacity:1;right:-300px}}
	@keyframes fade1Jon{0%{opacity:0}100%{opacity:1}}
	@keyframes fade2Jon{0%,25%{opacity:0}100%{opacity:1}}
	@keyframes floatingJon{0%{transform:translateY(0px)}50%{transform:translateY(-30px)}100%{transform:translateY(0px)}}
	@keyframes rotateJon{0%{transform:rotate(0)}100%{transform:rotate(360deg)}}
