	@keyframes imgBigBlurEnter{0%{filter:blur(100px);opacity: 0}100%{filter:blur(0px);opacity: 1}}
	@keyframes moveLeftRight{0%{left:200px}50%{left:300px}100%{left:200px}}

</style>

<script>
import Start from './views/Start.vue'
import Index from './views/Index.vue'
import About from './views/About.vue'

export default {
	data(){
		return {
			posY: 0,
			posX: 0,
			play: false,
			forwards: true,
			speed: 0,
			runVertical: 'none',
			maxSpeed: 5,
			xLength: 10000,
			progress: 0,
			coin:[
				{id: 1, val: 50, x: 100, y: 0, isReach: false},
				{id: 2, val: 100, x: 500, y: 100, isReach: false},
				{id: 2, val: 50, x: 1000, y: -100, isReach: false}
			],
			score: 0,
			showPoint: {
				index:{ minX: 300, maxX: 3000 },
				about:{ minX: 1500, maxX: 10000 }
			}
		}
	},
	components: {
		Start, Index, About
	},
	mounted(){
		setInterval(this.slider, 20)

		window.addEventListener('keydown', e => {
			switch(e.key){
				case 'ArrowRight':
					this.run()
					break

				case 'ArrowLeft':
					this.back()
					break
					
				case 'ArrowUp':
					this.up()
					break
					
				case 'ArrowDown':
					this.down()
					break
					
				default:
					this.stop()
			}
		})

		this.drawCoin()
	},
	methods: {
		slider(){
			if(this.play){
				let css = this.posY+'px 0 0 '+parseInt(this.posX * -1)+'px'

				this.posX += this.speed
				if(this.runVertical === 'up'){
					if(this.forwards){
						this.posY += this.speed
					} else {
						this.posY -= this.speed
					}
				} else if(this.runVertical === 'down'){
					if(this.forwards){
						this.posY -= this.speed
					} else {
						this.posY += this.speed
					}
				}

				let progress = parseInt(this.posX / this.xLength * 100)
				if(progress > 100){
					progress = 100
				} else if(progress < 0){
					progress = 0
				}
				this.progress = progress
				document.querySelector('#slide').style.margin = css

				this.getCoin()
			}
		},
		run(){
			this.play = true
			if(this.speed < this.maxSpeed && this.runVertical === 'none'){
				this.speed += 1
			}
			if(this.speed > 0){
				this.forwards = true
			}
			this.runVertical = 'none'
			this.animateFire()
		},
		stop(){
			this.runVertical = 'none'
			this.play = false
			this.speed = 0
			this.animateFire()
		},
		back(){
			this.play = true
			if(this.speed > this.maxSpeed*-1 && this.runVertical === 'none'){
				this.speed -= 1
			}
			if(this.speed < 0){
				this.forwards = false
			}
			this.runVertical = 'none'
			this.animateFire()
		},
		up(){
			this.runVertical = 'up'
			if(this.speed > 0){
				this.posY += this.speed
			} else {
				this.posY -= this.speed
			}
		},
		down(){
			this.runVertical = 'down'
			if(this.speed > 0){
				this.posY -= this.speed
			} else {
				this.posY += this.speed
			}
		},
		animateFire(){
			const el = document.querySelector('.rocket-fire')
			let anim = ''
			let speed = Math.sqrt(this.speed*this.speed)
			switch(speed){
				case 0:
					anim = 'blit-0'
					break
				case 1:
					anim = 'blit-1'
					break
				case 2:
					anim = 'blit-2'
					break
				default:
					anim = 'blit-3'
					break
			}
			el.classList.remove('blit-0')
			el.classList.remove('blit-1')
			el.classList.remove('blit-2')
			el.classList.remove('blit-3')
			el.classList.add(anim)
		},
		drawCoin(){
			// supaya sesuai sama roket (ditengah2)
			let centerX = window.innerWidth / 2
			let centerY = window.innerHeight / 2

			let coinArea = document.querySelector('#coin-area')
			this.coin.forEach(e => {
				coinArea.innerHTML += `
					<div id="coin-${e.x}-${e.y}" class="coin coin-${e.val} anim-up-and-down" style="top:${e.y * -1 + centerY}px; left:${e.x+centerX}px">${e.val}</div>
				`
			})
		},
		getCoin(){
			let margin = 50
			this.coin.forEach(e => {
				if(!e.isReach){
					if(
					this.posX > e.x - margin && 
					this.posX < e.x + margin && 
					this.posY > e.y - margin &&
					this.posY < e.y + margin)
					{

						this.score += e.val
						e.isReach = true

						let el = document.querySelector(`#coin-${e.x}-${e.y}`)
						el.classList.remove('anim-up-and-down')
						el.classList.add('anim-hide-slide')
					}
				}
			})
		}
	}
}
</script>